.page {
  scroll-behavior: smooth;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

.page__body {
  margin: 0;
  padding: 0;
}

.header {
  white-space: nowrap;
  justify-content: space-between;
  height: 60px;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__link-logo {
  height: 40px;
  margin-block: 10px;
  padding-right: 5px;
  display: block;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  text-transform: uppercase;
  color: #060b35;
  line-height: 60px;
  text-decoration: none;
  transition-duration: .3s;
  display: block;
}

.nav__item:not(:last-child) {
  padding-right: 20px;
}

.nav__link:hover, .is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: relative;
  bottom: 4px;
}

.catalog {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .catalog {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .catalog {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .catalog {
    grid-template-columns: repeat(4, 200px);
  }
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 16px;
  transition-duration: .3s;
}

.card:hover {
  transform: scale(1.2);
}

.card__image {
  width: 160px;
  height: 134px;
  margin: 16px 3px 0;
  display: block;
}

.card__name {
  color: #060b35;
  margin: 0;
  padding-top: 40px;
  font-family: inherit;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  transition-duration: .3s;
  display: flex;
}

.card:hover .card__name {
  color: #34568b;
}

.card__code {
  color: #616070;
  margin: 0;
  padding-top: 4px;
  font-family: inherit;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__reviews {
  justify-content: space-between;
  padding-top: 18px;
  padding-left: 2px;
  font-family: inherit;
  display: flex;
}

.card__reviews-text {
  color: #060b35;
  margin: 0;
  font-family: inherit;
  font-size: 10px;
  font-weight: 400;
  line-height: 12px;
}

.card__price {
  justify-content: space-between;
  padding-top: 22px;
  font-family: inherit;
  display: flex;
}

.card__price-label {
  color: #616070;
  margin: 0;
  font-family: inherit;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-value {
  color: #060b35;
  margin: 0;
  font-family: inherit;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  color: #fff;
  text-transform: uppercase;
  text-align: center;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  margin-top: 16px;
  padding: 0;
  font-family: inherit;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  transition-duration: .3s;
}

.card__button:hover {
  cursor: pointer;
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}
